.profile__avatar-container {
    position: relative;
    width: 120px;
    max-height: 120px;
    min-height: 120px;
    background-repeat: no-repeat;
    background-position: center;
}

@media screen and (max-width: 700px) {
    .profile__avatar-container {
        margin: 0 auto;
        margin-bottom: 10px;
    }
}